"use strict";

import express from "express";

const userAC = require('./userActionController');
let User = require('./user.js');
const router = express.Router();

//route for all new User requests
router.post("/createUser", function (req, res) {

    let userInfo = req.body; //assigns user info to an object
    let tempUser = new User(userInfo); //creates a new User object with info

    userAC.createProfile(tempUser, function (response) { //attempts to create user and receives a success or error
        res.json(response);
    });


});

module.exports = router;
